@import url('https://fonts.googleapis.com/css?family=Rubik:700&display=swap');
@import "../../DefaultStyle/config.scss";
@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300&family=Raleway:ital,wght@0,100;0,300;0,400;1,400&display=swap');

.backdrop{
    height: inherit;
    background: $dark-blue;
    opacity: 0.6;
    margin: 10px;
    padding: 10px;
    // z-index: 8!important;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}