@import "../../DefaultStyle/config.scss";
$minwidth : 700px;
.mailIcon {
    position: fixed;
    z-index: 3;
    //border : 3px solid red;
    bottom : 15.1%; 
    right : 10%;
}

@include devices(tablet) {
    .mailIcon {
       bottom:  7.5%;
       right : 9%;
    }
    // code set for mobile    
}

.waves1 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    z-index: inherit;
    & .waves1Svg {
        position: relative;
        display: block;
        width: calc(100% + 1.3px);
        height: 243px;
        transform: rotateY(180deg);
    }
}

.waves {
    fill: $dark-blue;
}

.waves2 {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
    z-index: inherit;
    & .waves2Svg {
        position: relative;
        display: block;
        width: calc(129% + 1.3px);
        height: 174px;
    }
}