@import url('https://fonts.googleapis.com/css?family=Rubik:700&display=swap');
@import "../../DefaultStyle/config.scss";
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,300;0,400;1,400&display=swap');


.header{
    height: 4rem;
    margin: 0;
    border-radius: 10px;
    background: inherit;
    opacity: 1;
    z-index: 8;
    box-shadow: rgba(19, 13, 13, 0.54) 0px 3px 8px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: $font-Size;
    font-family: inherit;

    & .on{
        background-color: rgb(119, 255, 0);
        width: 15px;
        height: 15px;
        z-index: 10;
        border-radius: 50%;
        margin-left: 30px ;

    }

    & .off{
        background-color: rgb(254, 97, 30);
        width: 15px;
        height: 15px;
        z-index: 10;
        border-radius: 50%;
        margin-left: 30px ;

    }

    & .header_inner_body{
        width: 30%;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;

        & p{
            font-weight: $font-Weight-Bold;
            font-family:"Raleway", "sans-serif" ;
            font-size: 25px;
            z-index: 0;
            line-height: 1;
            word-wrap: break-word;
        }
    }

    & .header_inner_footer{
        margin-right: 30px;
        width: 50%;
        font-family: inherit;
        height: 80%;
        border-radius: 10px;
        & ul{
            display: flex;
            list-style: none;
            justify-content: space-around;
            align-items: center;
            height: 100%;
            width: 100%;
            & li{
                height: 100%;
                display: flex;
                align-items: center;
                padding: 25px;
                align-items: center;
                background-color: inherit;
                border-radius: 5px;
                
                & button {
                  align-items: center;
                  background-color: $main-color;
                  border: 1px solid $blue-shadow;
                  border-radius: 8.5px;
                  box-sizing: border-box;
                  color: #fff;
                  cursor: pointer;
                  display: flex;
                  font-family: $font-Family;
                  font-size: $font-Size;
                  font-weight: $font-Weight-Bold;
                  height: 48px;
                  justify-content: center;
                  line-height: 24px;
                  max-width: 100%;
                  padding: 0 25px;
                  position: relative;
                  text-align: center;
                  text-decoration: none;
                  user-select: none;
                  -webkit-user-select: none;
                  touch-action: manipulation;

                  &:after {
                    background-color: $blue-shadow;
                    border-radius: 8px;
                    content: "";
                    display: block;
                    height: 48px;
                    left: 0;
                    width: 100%;
                    position: absolute;
                    top: -2px;
                    transform: translate(8px, 8px);
                    transition: transform .2s ease-out;
                    z-index: -1;
                  }
  
                  &:hover:after {
                    transform: translate(0, 0);
                  }
  
                  &:active {
                    background-color: $dark-blue;
                    color: $light-blue;
                    outline: 0;
                  }
  
                  &:hover {
                    outline: 0;
                  }
  
                  @media (min-width: 768px) {
                    & {
                      padding: 0 40px;
                    }
                  }
                }
            }
        }
    }
}
