@import url('https://fonts.googleapis.com/css?family=Rubik:700&display=swap');
@import "../../DefaultStyle/config.scss";
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,300;0,400;1,400&display=swap');

.main_form_container{
    z-index: 14 !important;
    width: 40%;
    height: 100%;
    background: $blue;
    margin: auto;
    border-radius: 10px;
    border: 2px solid #000;

    & .form{
        height: inherit;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;

        & .formInput{
            border: 1px solid red;
            width: 100%;
            height: 45px;

            & input{
                width: inherit;
                height: inherit;
            }

            
        }
    }
}