@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;700&display=swap');
@import "../../DefaultStyle/config.scss";



.login {
   position: relative;
   margin : .5em;
   border-radius: 15px;
   background-color: $light-bg;
   display: flex;
   justify-content: center;
   align-items: center;
   width:  400px;
   height: 520px;
   font-family: inherit;
   text-align: center;

   & .mailIcon{
      position: fixed;
      z-index: 3;
      bottom: 120px;
      left: 64%;
      border: 3px solid #73AD21;
      width : 0;
      height : 0
   }
   
   & .loginBox {
        width : inherit;
        height: inherit;
        border-radius: inherit;
        margin: .5em;
        padding : 1.5em;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        font-family: inherit;

        & h1 {
           padding : 10px;
           font-size: 1.5em;
           text-align: center;
           font-family: inherit;
           user-select: none;
        }
        & h4 {
           padding: 5px;
           text-align: center;
           font-family: inherit;
           font-size: 1.1em;
           margin-top: 15px;
           font-weight: 400;
           user-select: none;
        }
        & .loginForm {
            height: inherit;
            width: inherit;
            // margin : .5em;
            padding : 1em 1.5em 1em 1.5em;
            display: flex;
            justify-content: space-around;
            align-items: center;
            flex-direction: column;
        }
   }
} 